import { Dialog, IconButton, Typography } from "@material-ui/core";
import MuiDialogTitle from "@material-ui/core/DialogTitle";
import CloseIcon from '@material-ui/icons/Close';
import { Form, Formik } from "formik";
import React from "react";
import { useStyles } from "./addCampaign.style";
import shirt from "assets/icons/shirt.png";


interface AddCampaignModalProps {
    open: boolean;
    onClose: () => void;
}

export const AddCampaignModal: React.FC<AddCampaignModalProps> = ({ open, onClose }) => {
    const c = useStyles();
    return (
        <Dialog className={c.modal} onClose={onClose} open={open}>
            <MuiDialogTitle className={c.modalTitle}>
                <Typography variant="h6">Add campaigns to your store</Typography>
                <IconButton aria-label="close" onClick={onClose} >
                    <CloseIcon fontSize="large" />
                </IconButton>
            </MuiDialogTitle>
            <div>
                <Formik
                    initialValues={{}}
                    onSubmit={() => { }}
                >
                    {
                        ({ resetForm }) => {
                            return (
                                <Form>
                                    <div className={c.container}>
                                        <div className={c.left}>
                                            <img src={shirt} className={c.img} />
                                            <div className={c.imgTxt}>
                                                <Typography style={{ fontWeight: 550, fontSize: 17, marginTop: "-20px", marginBottom: 10 }}>good</Typography>
                                                <Typography style={{ fontSize: 12 }}>0 sold (need 1 more)</Typography>
                                            </div>
                                        </div>
                                        {/* right */}
                                        <div className={c.right}>
                                            <div className={c.rightFirstChild}>
                                                <div style={{ display: 'flex', justifyContent: 'space-between', alignItems: "center" }}>
                                                    <div style={{ width: 20, height: 20, background: "#68CB98", borderRadius: "50%" }}></div>
                                                    <Typography style={{ fontWeight: 550, fontSize: 17 }}>Active</Typography>
                                                </div>
                                                <Typography style={{ fontSize: 12 }}>a day remaning</Typography>
                                            </div>
                                            <div className={c.plus}>+</div>
                                        </div>
                                    </div>
                                    <div style={{
                                        marginTop: "70px",
                                        marginBottom: "20px",
                                        display: "flex",
                                        justifyContent: 'flex-end',
                                        alignItems: "center"
                                    }}>
                                        <p style={{
                                            margin: 0, marginRight: 10, color: "#5E82A6", cursor: "pointer"
                                        }}
                                            onClick={() => {
                                                onClose()
                                                resetForm()
                                            }}
                                        >Cancel</p>
                                        <button className={`btn-primary btn-sm`}
                                            onClick={() => { }} >
                                            Save Settings
                                        </button>
                                    </div>
                                </Form>
                            )
                        }
                    }
                </Formik>
            </div>
        </Dialog>
    )
}