import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles(() => ({
    fieldLabel: {
        fontWeight: 600
    },
    field: {
        '& .MuiOutlinedInput-inputMarginDense::focus': {
            backgroundColor: "white"
        },
        '& .MuiOutlinedInput-inputMarginDense': {
            color: '#3d4853',
            lineHeight: 1.6,
            padding: ' 12px 16px',
            fontWeight: 800,
            fontSize: '1.5rem',
            "&:-webkit-autofill": {
                // for removing gray background color, when autofill
                WebkitBoxShadow: "0 0 0 1000px white inset"
            }
        },
        '& .MuiFormHelperText-root.Mui-error': {
            fontSize: '13px'
        }
    },
}))