import React from "react"
import { TextField } from 'formik-material-ui';
import { Field } from 'formik'
import { InputAdornment, Typography } from '@material-ui/core';
import { useStyles } from './BonfireField.style'
import clsx from "clsx";

interface BonfireFieldProps {
    label?: string;
    name: string;
    placeholder: string;
    type?: string;
    inputIcon?: React.ReactNode;
    style?: object;
    select?: boolean;
    id?: string;
    value?: any;
    size?: string;
    className?: any;
    disabled?: boolean;
}

export const BonfireField: React.FC<BonfireFieldProps> = ({ id, disabled, value, label, size, name, placeholder, style, type, inputIcon, select, children, className }) => {
    const classes = useStyles()
    return (
        <>
            <Typography variant="h5" className={classes.fieldLabel}>{label}</Typography>
            <Field
                id={id}
                style={{ ...style }}
                className={clsx(classes.field, className)}
                component={TextField}
                name={name}
                value={value}
                size={size}
                select={select}
                variant="outlined"
                margin="dense"
                placeholder={placeholder}
                type={`${type || "text"}`}
                fullWidth
                InputProps={{
                    endAdornment: (
                        <InputAdornment position="end">
                            {inputIcon}
                        </InputAdornment>
                    ),
                    disabled: disabled ? true : false,
                    // className:classes.input
                }}
                SelectProps={{
                    MenuProps: {
                        anchorOrigin: {
                            vertical: "bottom",
                            horizontal: "left",
                        },
                        getContentAnchorEl: null
                    }
                }}
            >{children}</Field>
        </>
    )
}