import { Dialog, IconButton, Typography } from "@material-ui/core";
import MuiDialogTitle from "@material-ui/core/DialogTitle";
import CloseIcon from '@material-ui/icons/Close';
import Done from "@material-ui/icons/Done";
import { api } from "api";
import { BonfireField } from "components/BonfireField/BonfireField";
import { Form, Formik } from "formik";
import React, { useEffect, useState } from "react";
import { useStyles } from "./settings.style";



interface SettingsModalProps {
    open: boolean;
    onClose: () => void;
}

interface SettingsDataProps {
    title: string;
    url: string;
    subtitle: string;
}

export const SettingsModal: React.FC<SettingsModalProps> = ({ open, onClose }) => {
    const c = useStyles();
    const [settingsData, setSettingsData] = useState<SettingsDataProps>();

    const getSettingsData = async () => {
        try {
            const axios = await api();
            const res = await axios.get("store");
            console.log(res)
            setSettingsData(res.data)
        } catch (error) {
            alert()
            console.log(error)
        }
    }

    useEffect(() => {
        getSettingsData()
    }, [])
    return (
        <Dialog className={c.modal} onClose={onClose} open={open}>
            <MuiDialogTitle className={c.modalTitle}>
                <Typography variant="h6">Store setting</Typography>
                <IconButton aria-label="close" onClick={onClose} >
                    <CloseIcon fontSize="large" />
                </IconButton>
            </MuiDialogTitle>
            <div>
                <Formik
                    initialValues={{
                        title: settingsData?.title,
                        url: settingsData?.url,
                        subtitle: settingsData?.subtitle,
                    }}
                    onSubmit={() => { }}
                >
                    {
                        ({ resetForm }) => {
                            return (
                                <Form>
                                    <div className={c.section}>
                                        <Typography variant="h5" style={{ letterSpacing: 3, fontWeight: 600 }}>TITLE</Typography>
                                        <BonfireField
                                            name="title"
                                            placeholder="trkn"
                                        />
                                        <Typography variant="subtitle1">Shown on your store and campaign pages.</Typography>
                                    </div>
                                    <div className={c.section}>
                                        <Typography variant="h5" style={{ letterSpacing: 3, fontWeight: 600 }}>URL</Typography>
                                        <div className={c.url}>
                                            <div className={c.urlBonf}>
                                                stylefactory.com/store/
                                        </div>
                                            <BonfireField
                                                name="url"
                                                placeholder="trkn"
                                                inputIcon={<Done style={{ color: "#3BCE95", fontSize: 20 }} />}
                                            />
                                        </div>
                                        <Typography variant="subtitle1">Share this link to let buyers find your store.</Typography>
                                    </div>
                                    <div className={c.section}>
                                        <Typography variant="h5" style={{ letterSpacing: 3, fontWeight: 600 }}>SUBTITLE</Typography>
                                        <BonfireField
                                            name="subtitle"
                                            placeholder="Official Merchandise"
                                        />
                                        <Typography variant="subtitle1">A short tagline displayed unnder your title.</Typography>
                                    </div>
                                    <div style={{
                                        marginTop: "40px",
                                        marginBottom: "20px",
                                        display: "flex",
                                        justifyContent: 'flex-end',
                                        alignItems: "center"
                                    }}>
                                        <p style={{
                                            margin: 0, marginRight: 10, color: "#5E82A6", cursor: "pointer"
                                        }}
                                            onClick={() => {
                                                onClose()
                                                resetForm()
                                            }}
                                        >Cancel</p>
                                        <button className={`btn-primary btn-sm`}
                                            onClick={() => { }} >
                                            Save Settings
                                        </button>
                                    </div>
                                </Form>
                            )
                        }
                    }
                </Formik>
            </div>
        </Dialog>
    )
}