import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles(() => ({
    modal: {
        "& .MuiDialogTitle-root": {
            padding: "16px 0px",
        },
        "& .MuiPaper-root": {
            width: "800px",
            maxWidth: "unset",
            padding: "20px 30px",
        }

    },
    section: {
        margin: "10px 0px 20px"
    },
    modalTitle: {
        "&>h2": {
            display: "flex",
            justifyContent: 'space-between',
            alignItems: 'center',
            "&>h6": {
                fontSize: 20,
                fontWeight: 600
            },
        }
    },
    url: {
        display: "flex",
        alignItems: "center",
        "& .MuiOutlinedInput-root": {
            borderBottomLeftRadius: "0px",
            borderTopLeftRadius: "0px"
        }
    },
    urlBonf: {
        minWidth: 240,
        fontWeight: 600,
        padding: 10,
        border: "1px solid #ccc",
        borderBottomLeftRadius: 4,
        borderTopLeftRadius: 4,
        backgroundColor: "#ececec",
        height: "42px",
        marginTop: 4,
        borderRight: 0,
        textAlign: "center"
    }
}))