import { makeStyles } from "@material-ui/core";

export const useStyles = makeStyles(() => ({
    modal: {
        "& .MuiDialogTitle-root": {
            padding: "16px 0px",
        },
        "& .MuiPaper-root": {
            width: "800px",
            maxWidth: "unset",
            padding: "20px 30px",
        }

    },
    modalTitle: {
        "&>h2": {
            display: "flex",
            justifyContent: 'space-between',
            alignItems: 'center',
            "&>h6": {
                fontSize: 20,
                fontWeight: 600
            },
        }
    },
    container: {
        width: "100%",
        display: "flex",
        justifyContent: 'space-between',
        border: "1px solid #BDCAD8",
        borderRadius: 10,
        padding: "40px 20px"
    },
    left: {
        display: "flex",
        justifyContent: 'flex-start',
    },
    img: {
        width: 100,
        height: 100,
        marginRight: 25
    },
    imgTxt: {
        display: "flex",
        flexDirection: "column",
        justifyContent: 'center',
    },
    right: {
        display: "flex",
        justifyContent: 'flex-end',
        alignItems: "center"
    },
    rightFirstChild: {
        width: 100,
    },
    plus: {
        marginLeft: 25,
        fontSize: 25,
        color: "#BDCAD8",
        fontWeight: 100,
        width: 30,
        height: 30,
        border: "1px solid #BDCAD8",
        borderRadius: "50%",
        textAlign: "center",
        "&:hover": {
            cursor: "pointer",
            backgroundColor: "#BDCAD8",
            color: "white"
        }

    }
}))