import { makeStyles } from "@material-ui/core";

export const styles = makeStyles((theme) => ({
    pageContainer: {

    },
    spBtwn: {
        display: "flex",
        justifyContent: 'space-between',
        alignItems: "center",
    },
    blackSection: {
        width: "100%",
        height: "400px",
        backgroundColor: "#3D4853",
        "& div": {
            width: "33%",
        },
        padding: "0px 50px"
    },
    storeContainer: {
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        flexDirection: "column",
        "& p": {
            color: "white"
        }
    },
    titleContainer: {
        display: "flex",
        alignItems: "center",
        justifyContent: "center",
        height: "100%",
        marginLeft: -50
    },
    storeName: {
        fontSize: 30,
        fontWeight: 600
    },
    storeNameUnder: {
        fontSize: 18,
        textTransform: "uppercase",
        marginLeft: "-40px !important"
    },
    editHeader: {
        color: "black",
        height: 40,
        backgroundColor: "white"
    },
    bioSectionContainer: {
        padding: `50px ${theme.spacing(10)}px`,
        marginBottom: "150px",
        '&>div>p, &>div>p': {
            margin: "0px"
        }
    },
    dashDashBorder: {
        border: "2px #AEAEAE dashed",
    },
    linkTxt: {
        color: "#5E82A6",
        marginBottom: "10px !important",
        marginTop: "2px !important"
    },
    inputFile: {
        "&": {
            position: "relative",
            width: "115px"
        },
        "&::-webkit-file-upload-button": {
            visibility: "hidden",
            paddingRight: "30px"
        },
        "&::before": {
            position: "absolute",
            "-webkit-user-select": "none",
            content: '"Upload a logo  +"',
            color: "#5E82A6",
            fontSize: 15.6,
            display: "inline-block",
            outline: "none",
        }
    },
    tooltip: {
        "&>* > span": {
            fontSize: "20px"
        }
    },
    textArea: {
        overflow: "hidden",
        height: "150px",
        padding: "20px",
        resize: "none",
        fontSize: 15,
        width: "60%",
        "&::-webkit-input-placeholder": {
            color: "#5E82A6",
        },
        "&:-moz-placeholder": { /* Upto Firefox 18, Deprecated in Firefox 19  */
            color: "#5E82A6",
        },
        "&::-moz-placeholder": {  /* Firefox 19+ */
            color: "#5E82A6",
        },
        "&:-ms-input-placeholder": {
            color: "#5E82A6",
        }
    },
    dashedCampaign: {
        border: "2px #AEAEAE dashed",
        width: "80%",
        display: "flex",
        justifyContent: 'center',
        flexDirection: "column",
        alignItems: "center",
        padding: "10px 20px",
        "&:hover": {
            backgroundColor: "#f8f8f8",
            cursor: "pointer"
        }
    },
    plus: {
        width: 40,
        height: 40,
        color: "white",
        borderRadius: "50%",
        backgroundColor: theme.palette.primary.main,
        textAlign: 'center',
        fontSize: 40,
        lineHeight: 0,
        paddingTop: "20px"
    },
    lastSection: {
        width: "100%",
        height: 300,
        padding: "60px 500px",
        background: "#eee"
    },
    lastFirst: {
        width: "250px",
        padding: "20px 40px"
    },
    lastSec: {
        width: "600px",
        marginTop: "50px",
        padding: "20px 40px"
    },
    // header
    container: {
        padding: "0px 30px",
        alignItems: "center",
        height: 100
    },
    leftImg: {
        marginRight: "15px"
    },
    blueTxt: {
        color: "#5E82A6",
        margin: "0px !important",
        fontSize: "19px"
    },
    spBtwnHeader: {
        display: "flex",
        justifyContent: "space-between",
    },
    left: {
        display: "flex",
        justifyContent: "start",
        alignItems: "center"
    },
    cntrd: {
        display: "flex",
        justifyContent: 'center',
    },
    middle: {
        "& p": {
            margin: "0px !important",
            textAlign: "center"
        }
    },
    storeNameHeader: {
        fontSize: "19px",
        letterSpacing: 2,
        fontWeight: 600
    },
    df: {
        display: "flex",
        alignItems: "center"
    },
    img: {
        width: 30,
        height: 30,
        borderRadius: "50%"
    },
    right: {
        minWidth: 340,
        display: "flex",
        justifyContent: 'space-between',
    },
    url: {
        display: "flex",
        alignItems: "center",
        "& .MuiOutlinedInput-root": {
            borderBottomLeftRadius: "0px",
            borderTopLeftRadius: "0px"
        }
    },
    urlBonf: {
        minWidth: 240,
        fontWeight: 600,
        padding: 10,
        paddingRight: 0,
        borderBottomLeftRadius: 4,
        borderTopLeftRadius: 4,
        height: 42,
        marginTop: 4,
        borderRight: 0,
        textAlign: "center"
    },
    imgEdit: {
        width: 20,
        height: 20,
        marginTop: 8,
        marginLeft: 5,
        "&:hover": {
            cursor: "pointer"
        }
    },
    subtitleInput: {
        width: "100%",
        backgroundColor: "transparent",
        color: "white",
        fontSize: 30
    },
    titleInput: {
        backgroundColor: "transparent",
        fontSize: 16,
        fontWeight: 600,
        border: "1px solid #eee",
        borderTopRightRadius: 5,
        borderBottomRightRadius: 5,
        padding: "0px 5px"
    }
}))